@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

html, body {
  background: black;
  color: #00b800;
  margin:0;
  padding: 0;
  padding-top: 10px;
  font-family: 'Press Start 2P', cursive;
  text-align: center;
}

body {
  font-weight: 400;
}

button {
  font-family: 'Press Start 2P', cursive;
  display: block;
  margin: 0 auto;
  border: none;
  background: #a80020;
  background: #00b800;
  padding: 10px 20px;
  text-transform: uppercase;
}

h1 {
  font-size: 20px;
  text-align:center;
  margin: 20px 0;
  line-height: 1.6;
}

textarea {
  font-family: 'Press Start 2P', cursive;
  border: 3px solid black;
  width: 90%;
  margin: 0 auto;
  outline: none;
  height: 200px;
  background: #00b800;
  padding: 10px;
  width: 600px;
}


textarea:disabled,
button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

@media screen and (max-width: 860px) {
  textarea {
    width: 80%;
    border-radius: 10px;
  }
}